@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.quote{width:90%;margin:auto;display:flex;flex-direction:column;font-family:"Poppins", sans-serif;font-size:18px}.quote #text{font-size:2em;display:flex;justify-content:center;text-align:center}.quote #text:before,.quote #text:after{content:'"'}.quote #author{display:flex;justify-content:flex-end;margin-right:15%;font-style:italic}.quote #author:before,.quote #author:after{content:"--"}

.btns{display:flex;flex-direction:row;justify-content:space-between;width:80%;margin:auto}.btn{height:3rem;border:none;border-radius:15px;font-family:"Poppins", sans-serif;color:white;font-size:1rem}#new-quote{width:15%}

.main-app{width:100%;height:100vh;padding-top:5%}#quote-box{width:80%;margin:auto;background-color:white;border-radius:15px;height:70vh;display:flex;flex-direction:column;grid-row-gap:2rem;row-gap:2rem;padding-top:3rem}#tweet-quote{font-family:"Poppins", sans-serif;width:5rem;font-size:1rem;display:flex;align-items:center;justify-content:center;text-decoration:none}

